@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.NavMain {
  background-color: transparent;
  color: rgb(57, 57, 57);
  display: flex;
  align-items: center;
  padding: 20px;
  height: 70px;
}

.NavMain .nav-list {
  margin-left: auto;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

/* nav .navtop {
  height: 15vh;
  background-color: var(--main-color);
  color: var(--second-color);
} */

.NavTop {
  min-height: 10vh;
  max-width: 100vw;
  background-color: var(--main-color);
  color: var(--second-color);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0vh;
  min-width: 100vw;
  z-index: 1;
}

.NavTop i {
  background-color: var(--main-color);
  color: var(--second-color);
}

.NavBottom {
  height: 10vh;
  max-width: 100vw;
  background-color: white;
  color: var(--main-color);
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-top: 2px solid var(--main-color);
  position: fixed;
  bottom: 0vh;
  width: 100vw;
}

.NavBottom i {
  color: var(--main-color);
}

.NavBottom i:hover {
  color: var(--second-color);
}

.NavSearch {
  min-height: 10vh;
  max-width: 100vw;
  background-color: var(--main-color);
  color: var(--second-color);
  display: flex;
  /* display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10%; */
  /* grid-auto-rows: minmax(100px, auto); */
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0vh;
  z-index: 1;
  min-width: 100vw;
}

select,
option {
  background-color: var(--main-color);
  color: var(--second-color);
  border: none;
  font-size: 0.8rem;
}

.Extras {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 80vh;
  margin: 13vh 5vw;
  padding: 5%;
}

.Extras button {
  margin: 5vh 0;
  width: 45vw;
  height: 5vh;
  font-weight: bold;
  align-self: center;
}

h1 {
  color: var(--second-color);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #0c084c;
  color: var(--main-color);
}

html {
  font-family: "Manrope", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

/* ADDED BY AVD */

:root {
  --main-color: #0c084c;
  --second-color: #ffa41b;
}

/* style={{props.path === "/created/coming-events" ? '#ffa41b' : '#0c084c'}} */

i {
  color: black;
  font-size: 4vh;
}

/* .cardEvent {
  display: flex;
  margin: 30px;
}

.cardEvent img {
  width: 40vw;
  margin-right: 30px;
} */

.event.image {
  -webkit-filter: grayscale(100%);
}

.body {
  margin: 13vh 5vw;
  border: 2px solid #0c084c;
  border: 2px solid var(--main-color);
  border-radius: 20px;
  padding: 5%;
}

button {
  background-color: #ffa41b;
  background-color: var(--second-color);
  color: #0c084c;
  color: var(--main-color);
  border: none;
  border-radius: 5px;
}

.auth {
  margin-top: 10vh;
  margin-bottom: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: grid;
  grid-template-rows: 1fr 1fr;
  height: 60vh;
}

.auth button {
  width: 45vw;
  height: 10vh;
  font-size: 1.5rem;
  font-weight: bold;
}

#profileImg {
  border-radius: 50%;
  object-fit: cover;
}

.popup {
  width: 55vw;
  height: 30vh;
  background-color: #ffa41b;
  background-color: var(--second-color);
  color: white;
  position: fixed;
  top: 30vh;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup p {
  width: 40vw;
  font-weight: bold;
  text-align: center;
}

.middle strong {
  align-self: center;
}

/* SIGN IN / SIGN UP */

.form {
  display: flex;
  flex-direction: column;
  height: 80vh;
  margin: 10vh 10vw;
  justify-content: center;
}

.form .field {
  display: flex;
  flex-direction: column;
  margin: 3vh 0;
}

label {
  font-weight: bold;
  margin-bottom: 2vh;
}

input {
  width: 78vw;
}

.hidden {
  display: none;
}

i {
  color: var(--main-color);
}

.upload {
  display: flex;
  justify-content: space-between;
}

span {
  visibility: hidden;
}

.form button {
  width: 45vw;
  height: 5vh;
  /* font-size: 1.5rem; */
  font-weight: bold;
  align-self: center;
}

.form p,
h3 {
  align-self: center;
}

/* CREATE EVENT */

.fixNav {
  height: 10vh;
}

.fixBot {
  height: 10vh;
  position: relative;
  bottom: 0vh;
}

/* .form.create {
  position: relative;
  top: 10vh;
  bottom: 10vh;
} */

.form.create .field {
  margin: 1vh 0;
}

.form.create select,
option {
  background-color: white;
  color: var(--main-color);
  border: none;
}

.main {
  position: relative;
  top: 12vh;
}

.main .field.upload {
  margin-bottom: 0;
}

/* FORM CREATE */
.formcreate {
  display: flex;
  flex-direction: column;
}

.formcreate .field {
  display: flex;
  flex-direction: column;
  margin: 1vh 0;
}

.formcreate select,
option {
  background-color: white;
  color: var(--main-color);
  border: none;
}

.field.full {
  display: flex;
  flex-direction: row;
}

.label.full {
  max-width: 40vw;
}

.input.full {
  max-width: 10vw;
}

.middle {
  margin: 3vh 0 10vh 0;
  min-height: 90vh;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  max-width: 100vw;
  /* padding: 5%; */
}

.card {
  border: 2px solid var(--main-color);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  margin: 0 6% 4% 6%;
  padding: 4% 5%;
}

.card.one {
  min-height: 75vh;
  position: relative;
  margin: 5% 6%;
}

.center {
  align-self: center;
  font-size: 1.2rem;
  margin-bottom: 0.5vh;
  /* background-image: repeating-linear-gradient(
    -45deg,
    transparent 0 5px,
    var(--second-color) 10px 10px
  ); */
}

/* .center:before,
.center:after {
  background-image: repeating-linear-gradient(
    -45deg,
    transparent 0 30px,
    var(--second-color) 10px 40px
  );
} */
/* .center:before {
  margin-right: 0.3rem;
}
.center:after {
  margin-left: 0.3rem;
} */

.number {
  display: flex;
  justify-content: center;
  width: 100vw;
  margin: 1vh 0;
}

.owner {
  display: flex;
  margin-bottom: 2vh;
}

#profileImg {
  height: 3vh;
}

.middle .event,
.image {
  align-self: center;
  width: 50vw;
  margin: 6% 0%;
}

.interested {
  position: absolute;
  bottom: 3vh;
  left: 30%;
  align-self: center;
  padding: 2% 4%;
}

hr {
  border-top: solid 1px var(--second-color);
  height: 2px;
  color: var(--second-color);
  background-color: var(--second-color);
  /* width: 50%;  */
  border: none;
  margin-bottom: 2vh;
}

.card,
.multiple {
  max-height: 25vh;
}

.cardEvent {
  display: grid;
  grid-template-rows: 6fr 2fr 1fr;
  /* row-gap: 1vw; */
  font-size: 0.8rem;
  max-height: 20vh;
}

.eventTop {
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-column-gap: 3vw;
  -webkit-column-gap: 3vw;
          column-gap: 3vw;
  max-height: 15vh;
}

.eventImg img {
  width: 25vw;
  height: 12vh;
  object-fit: cover;
}

.description p {
  max-width: 75vw;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

/* PROFILE */

.btn.profile {
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 1;
}

.btn.profile button {
  border: 1px solid var(--second-color);
}

.link.profile {
  width: 50vw;
  height: 6vh;
  border-radius: 0%;
  font-weight: bold;
  border: 1px solid white;
}

/* CREATED EVENTS */

/* .card.multiple.create {
  display: flex;
  flex-direction: row;
} */

.card.multiple.create:last-child {
  display: flex;
  flex-direction: row;
}

.manage.event {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.manage.event i {
  font-size: 2vh;
}

.eventManage {
  display: flex;
  justify-content: center;
}

/* FAKE DIV */

.invisible {
  height: 10vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  position: static;
  visibility: hidden;
}

.invisibleBig {
  height: 16vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  position: static;
  /* visibility: hidden; */
  display: flex;
  justify-content: end;
  padding-top: 10vh;
}

/* CONVERSATION */
.all.conv {
  display: flex;
  flex-direction: column;
  overflow: scroll;
}

.event.conv {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2vh;
}

.conv.message {
  margin: 0 1vh;
  font-size: 0.7rem;
  width: 50vw;
}

.conv.message p {
  overflow: hidden;
}

.conv.date {
  font-size: 0.7rem;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.conv.date i {
  font-size: 2vh;
  padding-top: 1vh;
}

/* MESSAGES */

#form-messages {
  display: flex;
  justify-content: space-between;
  height: 10vh;
}

.messages input {
  max-width: 65vw;
  padding: 0 1vw;
  /* border-radius: 5px; */
}

.messages button {
  background-color: transparent;
  margin-left: 3vw;
}

.messages button i {
  color: var(--second-color);
  font-size: 3vh;
}

.author.message {
  display: flex;
}

.author.message strong {
  margin-left: 2vw;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
ul[class],
ol[class] {
  padding: 0;
}
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
figure,
blockquote,
dl,
dd {
  margin: 0;
}
html {
  scroll-behavior: smooth;
}
body {
  /*min-height:100vh*/
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}
ul[class],
ol[class] {
  list-style: none;
}
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}
img {
  max-width: 100%;
  display: block;
}
article > * + * {
  margin-top: 1em;
}
input,
button,
textarea,
select {
  font: inherit;
}
img:not([alt]) {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

